<template>
  <!--   -->
  <div class="orders">
    <!-- Breadcrumb  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-row>
        <!--  -->
        <el-col :span="10">
          <!--  -->
          <el-input placeholder="" v-model="inputData">
            <el-button slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!-- table -->
      <el-table :data="ordersList"  stripe border >
        <el-table-column label="#" type="index"></el-table-column>
        <el-table-column prop="order_number" label="" width="260px"></el-table-column>
        <el-table-column prop="order_price" label="" ></el-table-column>
        <el-table-column prop="pay_status" label="">
          <template slot-scope="scope">
            <el-tag type="danger" v-if="scope.row.pay_status == '0'"></el-tag>
            <el-tag type="danger" v-else="scope.row.pay_status == '1'"></el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="is_send" label=""></el-table-column>
        <el-table-column prop="create_time" label="">
          <template slot-scope="scope">{{scope.row.create_time | dataFormat}}</template>
        </el-table-column>
        <el-table-column label="" width="150px">
          <template slot-scope="scope">
            <el-button @click="showEditDialog" type="primary" size="mini" icon="el-icon-edit"></el-button>
            <el-button @click="showLocationDialog" type="success" size="mini" icon="el-icon-location-outline"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- Pagination  -->
      <!-- handleSizeChange   pagesize
      handleCurrentChange    pagenum 
      current-page 
      page-size  -->
       <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="ordersInfo.pagenum"
      :page-sizes="[5, 10, 15, 30]"
      :page-size="ordersInfo.pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
    </el-card>

    <!--   -->
    <el-dialog title="" :visible.sync="EditDialogVisible" @close="EidtDialogClose" width="30%">
      <!-- form -->
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="100px" >
        <el-form-item label="/" prop="address">
          <!-- Cascader       options  -->
          <el-cascader
            v-model="editForm.address"
            :options="cityData"
            >
          </el-cascader>
        </el-form-item>
        <el-form-item label="" prop="DetailedAddress">
          <el-input v-model="editForm.DetailedAddress"></el-input>
        </el-form-item>
      </el-form>
      
      <span slot="footer" class="dialog-footer">
        <el-button @click="EditDialogVisible = false"> </el-button>
        <el-button type="primary" @click="EditDialogVisible = false"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="locationDialogVisible" width="30%">
      <!-- Timeline  -->
      <el-timeline>
        <el-timeline-item
          v-for="(item, index) in progressInfo"
          :key="index"
          :icon="item.icon"
          :type="item.type"
          :color="item.color"
          :size="item.size"
          :timestamp="item.time">
          {{item.context}}
        </el-timeline-item>
      </el-timeline>
      <span slot="footer" class="dialog-footer">
        <el-button @click="locationDialogVisible = false"> </el-button>
        <el-button type="primary" @click="locationDialogVisible = false"> </el-button>
      </span>
    </el-dialog>

  </div>
</template>

<script>
// 1 citydata.js   
import cityData from '@/common/citydata.js'


export default {
  data() {
    return {
      // 2  citydata.js
      cityData: cityData,

      // 
      ordersList: [],
      // input 
      inputData: '',
      //  
      ordersInfo: {
        // 
        query: '',
        // 
        pagenum: 1,
        // 
        pagesize: 10,
      },
      // 
      total: 0, 

      //  
      EditDialogVisible: false,
      //    :model   v-model
      editForm: {
        // /
        address: [],
        // 
        DetailedAddress: '',
      },
      //    prop
      editFormRules: {
        address: [
           { required: true, message: '/', trigger: 'blur' },
        ],
        DetailedAddress: [
           { required: true, message: '', trigger: 'blur' },
        ]
      },

      //  
      locationDialogVisible: false,
      // 
      progressInfo: [],
    }
  },
  created() {
    // 
    this.getOrdersList()
  },
  methods: {
    // 
    async getOrdersList() {
      const {data: res} = await this.$http.get('orders', {params: this.ordersInfo})
      console.log(res)
      if(res.meta.status !== 200) this.$message.error('')
      this.$message.success('')
      this.ordersList = res.data.goods
      this.total = res.data.total
    },
    
    // 
    // 
    handleSizeChange(newsize) {
      console.log(newsize + '  ')
      this.ordersInfo.pagesize = newsize
      this.getOrdersList()
    },
    // 
    handleCurrentChange(newpage) {
      console.log(newpage + '   ')
      this.ordersInfo.pagenum = newpage
      this.getOrdersList()
    },

    // 
    showEditDialog() {
      this.EditDialogVisible = true
    },  
    //  
    EidtDialogClose() {
      //  form
      this.$refs.editFormRef.resetFields()
    },
    //  
    async showLocationDialog() {
      // 
      // const {data: res} = await this.$http.get('/kuaidi/1106975712662')
      // console.log(res)
      // if(res.meta.status !== 200) return this.$message.error('')
      // this.$message.success('')
      // this.progressInfo = res.data
      this.locationDialogVisible = true
      this.progressInfo = [
        {
          time: '2018-05-10 09:39:00',
          ftime: '2018-05-10 09:39:00',
          context: ',,',
          location: '',
          size: 'large',
          color: '#0bbd87',
          icon: 'el-icon-more',
          type: 'primary',
        },
        {
          time: '2018-05-10 08:23:00',
          ftime: '2018-05-10 08:23:00',
          context: '  95338',
          location: '',
          size: 'normal',
          color: '#0bbd87',
          icon: 'el-icon-more',
          type: 'success',
        },
        {
          time: '2018-05-10 07:32:00',
          ftime: '2018-05-10 07:32:00',
          context: ' ',
          location: '',
          size: 'large',
          color: '#0bbd87',
          icon: 'el-icon-s-opportunity',
          type: 'warning',
        },
        {
          time: '2018-05-10 02:03:00',
          ftime: '2018-05-10 02:03:00',
          context: ', ',
          location: '',
          size: 'large',
          color: 'hotpink',
          icon: 'el-icon-loading',
          type: 'info',
        },
      ]
    },
  },
}
</script>

<style scoped>
.el-card,
.el-table {
  margin-top: 1rem;
}
</style>